import {
  ALIGN_LEFT_HOT_KEY,
  ALIGN_CENTER_HOT_KEY,
  ALIGN_RIGHT_HOT_KEY,
  ALIGN_JUSTIFY_HOT_KEY,
} from './hotkeyTips';

export const shortcut = {
  left: ALIGN_LEFT_HOT_KEY,
  right: ALIGN_RIGHT_HOT_KEY,
  center: ALIGN_CENTER_HOT_KEY,
  justify: ALIGN_JUSTIFY_HOT_KEY,
};

export default function getTipsByType(type: string, locale: Record<string, any>) {
  return `${locale[`dropdown${type[0].toUpperCase()}${type.slice(1)}`]} ${shortcut[type]}`;
}
